<template>
    <div class="container auth_register">
        <form @submit.prevent="register">
            <h4 class="mb-3 text-center"></h4>
            <span class="mb-2"></span>
            <label for=""></label>
            <input type="text" placeholder="" v-model="name" required><br/>
            <label for="" class="mt-2"></label>
            <input type="email" placeholder="email" v-model="email" required><br/>
            <label for="" class="mt-2"></label>
            <input type="password" placeholder="" v-model="password" required minlength="6"><br/>
            <label for="" class="mt-2"></label>
            <input type="password" placeholder="" v-model="confirm_password" required minlength="6"><br/>
            <button type="submit" class="mt-4 auth_login_btn"></button>
        </form>
    </div>
</template>

<script>
import {mapState} from 'vuex';
export default {
    name:'register',
    data(){
        return {
            name:'',
            email:'',
            password:'',
            confirm_password:'',
        }
    },
    methods:{
        register(){
            this.$store.commit('auth/register',{email:this.email,password:this.password,confirm_password:this.confirm_password,name:this.name})
        }
    },
    computed:{
        ...mapState({
            register_success: state =>state.auth.register_success
        })
    }
}
</script>