import {firebaseAuth} from '../../main.js';

const state = {
    islogin:'',
    // register_success:'',
}

const mutations = {
    //
    login(state,payload){
        firebaseAuth
        .signInWithEmailAndPassword(payload.email , payload.password)
        .then(()=>{
            // console.log(firebaseAuth.currentUser.emailVerified)
            if(firebaseAuth.currentUser.emailVerified){
                alert('');
                state.islogin=true;
                payload.router.push({path:'/'})
            }else{
                firebaseAuth.signOut()
                alert('')
                location.reload()
            }
        })
        .catch(error => {
            error.message.includes('password') ? alert('') : alert('')
        })
      },

    //  
    logout(){
        firebaseAuth
        .signOut()
        .then(() => {
          alert('');
          state.islogin=''
        })
        .catch(error => {
          alert(error.message);
        });   
    },

    //
    register(state,payload){
        if(payload.password == payload.confirm_password){
            firebaseAuth
            .createUserWithEmailAndPassword(payload.email, payload.password)
            .then((res) => {
                res.user.updateProfile({
                    displayName:payload.name,
                })
                .then(() => {
                    const user = firebaseAuth.currentUser;
                    user.sendEmailVerification();
                    firebaseAuth.signOut()
                    alert('!');
                    // state.register_success = true;
                    window.location.href="/login"
                })
            })
            .catch(error => {
                error.message.includes('already in use')? alert('!') : alert(error.message);
            });
        }else{
            alert('')
        }
    },

    //
    checkLogin(state){
        firebaseAuth.onAuthStateChanged(function(user){
            if(user){
                state.islogin = true
            }
            // console.log(firebaseAuth.currentUser)
        })
    },

    //email
    sendEmail(state,payload){
        firebaseAuth
        .sendPasswordResetEmail(payload.email)
        .then(()=>{
            alert('!')
            location.reload()
        })
        .catch(error => {
            error.message.includes('no user') ? alert('') : alert(error.message)
        })
    }

}

const getters = {
}

export default {
    namespaced: true,
    state,
    mutations,
    getters
}